.footer {
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 165px;
}

.footer-section {
  width: 255px;
  height: 128px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 23px;
  color: var(--white);
  box-sizing: border-box;
}

.footer-title {
  font-family: 'League Spartan';
  font-weight: 700;
  font-size: 11px;
  line-height: 25px;
  letter-spacing: 1px;
  text-transform: uppercase;
  opacity: 0.5;
  margin: 0;
}

.footer-data {
  font-family: 'Antonio';
  font-weight: 400;
  font-size: 40px;
  line-height: 52px;
  letter-spacing: -1.5px;
  text-transform: uppercase;
  margin: 0;
}