.navbar {
  color: var(--white);
  height: 85px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Antonio', sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 36px;
  letter-spacing: -1.05px;
  text-transform: uppercase; 
  padding: 0 32px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);

}

.planets-list {
  display: flex;
  justify-content: space-between;
  list-style: none;   
}

.planet {
  font-family: 'Spartan';
  font-weight: 700;
  font-size: 11px;
  line-height: 25px;
  letter-spacing: 1px;
  margin: 0 32px;
  cursor: pointer;
  opacity: 0.75;
  transition: all ease 0.5s;
}

.planet:hover {
  opacity: 1;
}