.main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 126px;
  margin-bottom: 87px;
  padding: 0 165px;
  height: 540px;
}

.image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 100%;
  width: 750px;
}

.image-planet {
  outline: 3px solid rgb(61, 195, 61);
  border-radius: 50%;
}

.saturn {
  width: 100%;
}

.image-planet-internal {
  outline: 3px solid rgb(61, 195, 61);
  outline-offset: -5px;
  border-radius: 50% 0 0 50%;
}

.saturn-internal {
  width: 100%;
}

.image-planet-geology {
  position: absolute;
  width: 163px;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
}

.info-container {
  width: 350px;
}

.planet-name {
  font-family: 'Antonio';
  font-style: normal;
  font-weight: 400;
  font-size: 80px;
  line-height: 104px;
  text-transform: uppercase;
  margin-bottom: 23px;
}

.planet-overview {
  font-family: 'Spartan Leauge';
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
  margin-bottom: 24px;
  min-height: 150px;
}

.wiki {
  margin-bottom: 39px;
}

.wiki-name {
  display: inline;
  font-family: 'Spartan League';
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
}

.wiki-name-span {
  opacity: 0.5;
  margin-right: 8px;
}

.wiki-link {
  font-family: 'Spartan';
  font-weight: 700;
  font-size: 14px;
  line-height: 25px;
  color: var(--white);
}

.img-source {
  width: 12px;
  vertical-align: middle;
  margin-left: 8px;
}


.menu {
  font-family: 'Spartan League';
  font-weight: 700;
  font-size: 12px;
  line-height: 25px;
  letter-spacing: 2.57143px;
  text-transform: uppercase;
  width: 350px;
}

.menu-item {
  height: 48px;
  border: 1px solid rgba(255, 255, 255, 0.2); 
  padding-top: 12px;
  padding-left: 28px;
  cursor: pointer;
  box-sizing: border-box;
  transition: all ease-in 0.3s;
}

.menu-item:hover {
  background-color: rgba(216, 216, 216, 0.2);
}

.menu-text-span {
  opacity: 0.5;
  margin-right: 28px;
}